@import url(https://fonts.googleapis.com/css?family=Chilanka&display=swap);
* {
  box-sizing: border-box; }

body {
  margin: 0;
  padding: 0;
  font-family: 'Chilanka', cursive;
  width: 100%;
  height: 100vh;
  background: url(/Github-Cart/static/media/color-design-flora-1166644.095820eb.jpg);
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat; }


nav {
  padding: 8px; }
  nav ul {
    float: right; }
    nav ul li {
      display: inline-block; }
      nav ul li a {
        display: inline-block;
        outline: none;
        color: #000;
        font-size: 14px;
        font-weight: 600;
        letter-spacing: 1.2px;
        text-transform: uppercase;
        text-decoration: none; }
    nav ul li:not(:first-child) {
      margin-left: 48px; }
    nav ul li:last-child {
      margin-right: 24px; }

.logo {
  float: left;
  padding: 8px;
  margin: 8px 0px 0px 16px; }
  .logo a {
    color: #000;
    font-size: 18px;
    font-weight: 600;
    letter-spacing: 0px;
    text-transform: uppercase;
    text-decoration: none; }

@media screen and (max-width: 864px) {
  .logo {
    padding: 8px 0px 0px 0px; }
  .nav-wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: -1;
    opacity: 0;
    background: #fff;
    transition: all .2s ease;
    -webkit-transition: all .2s ease;
    -moz-transition: all .2s ease;
    -ms-transition: all .2s ease; }
    .nav-wrapper ul {
      position: absolute;
      width: 100%;
      top: 50%;
      transform: translateY(-50%);
      -webkit-transform: translateY(-50%);
      -moz-transform: translateY(-50%);
      -ms-transform: translateY(-50%); }
      .nav-wrapper ul li {
        display: block;
        float: none;
        width: 100%;
        text-align: center;
        margin-bottom: 10px; }
        .nav-wrapper ul li a {
          padding: 10px 24px;
          opacity: 0;
          color: #000;
          font-size: 14px;
          font-weight: 600;
          letter-spacing: 1.2px;
          transform: translateX(-20px);
          -webkit-transform: translateX(-20px);
          -moz-transform: translateX(-20px);
          -ms-transform: translateX(-20px);
          transition: all .2s ease;
          -webkit-transition: all .2s ease;
          -moz-transition: all .2s ease;
          -ms-transition: all .2s ease; }
      .nav-wrapper ul li:nth-child(1) a {
        transition-delay: 0.2s;
        -webkit-transition-delay: 0.2s;
        -moz-transition-delay: 0.2s;
        -ms-transition-delay: 0.2s; }
      .nav-wrapper ul li:nth-child(2) a {
        transition-delay: 0.3s;
        -webkit-transition-delay: 0.3s;
        -moz-transition-delay: 0.3s;
        -ms-transition-delay: 0.3s; }
      .nav-wrapper ul li:nth-child(3) a {
        transition-delay: 0.4s;
        -webkit-transition-delay: 0.4s;
        -moz-transition-delay: 0.4s;
        -ms-transition-delay: 0.4s; }
      .nav-wrapper ul li:nth-child(4) a {
        transition-delay: 0.5s;
        -webkit-transition-delay: 0.5s;
        -moz-transition-delay: 0.5s;
        -ms-transition-delay: 0.5s; }
      .nav-wrapper ul li:not(:first-child) {
        margin-left: 0px; }
  .nav-btn {
    position: fixed;
    display: block;
    top: 10px;
    right: 10px;
    width: 48px;
    height: 48px;
    z-index: 9999;
    border-radius: 50%;
    cursor: pointer; }
    .nav-btn i {
      display: block;
      width: 20px;
      height: 2px;
      border-radius: 2px;
      background: #000;
      margin-left: 14px; }
    .nav-btn i:nth-child(1) {
      margin-top: 16px; }
    .nav-btn i:nth-child(2) {
      margin-top: 4px;
      height: 1.5px;
      opacity: 1; }
    .nav-btn i:nth-child(3) {
      margin-top: 4px; } }

#nav:checked + .nav-btn {
  transform: rotate(45deg);
  -webkit-transform: rotate(45deg);
  -moz-transform: rotate(45deg);
  -ms-transform: rotate(45deg); }
  #nav:checked + .nav-btn i {
    background: #000;
    -webkit-transition: -webkit-transform .2s ease;
    transition: -webkit-transform .2s ease;
    transition: transform .2s ease;
    transition: transform .2s ease, -webkit-transform .2s ease;
    -webkit-transition: transform .2s ease;
    -moz-transition: transform .2s ease;
    -ms-transition: transform .2s ease; }
  #nav:checked + .nav-btn i:nth-child(1) {
    transform: translateY(6px) rotate(180deg);
    -webkit-transform: translateY(6px) rotate(180deg);
    -moz-transform: translateY(6px) rotate(180deg);
    -ms-transform: translateY(6px) rotate(180deg); }
  #nav:checked + .nav-btn i:nth-child(2) {
    opacity: 0; }
  #nav:checked + .nav-btn i:nth-child(3) {
    transform: translateY(-6px) rotate(90deg);
    -webkit-transform: translateY(-6px) rotate(90deg);
    -moz-transform: translateY(-6px) rotate(90deg);
    -ms-transform: translateY(-6px) rotate(90deg); }

#nav:checked ~ .nav-wrapper {
  z-index: 9990;
  opacity: 1; }
  #nav:checked ~ .nav-wrapper ul li a {
    opacity: 1;
    transform: translateX(0);
    -webkit-transform: translateX(0);
    -moz-transform: translateX(0);
    -ms-transform: translateX(0); }

.hidden {
  display: none; }

.github {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -30%);
  -webkit-transform: translate(-50%, -30%);
  -moz-transform: translate(-50%, -30%);
  -ms-transform: translate(-50%, -30%); }
  .github .github-title {
    color: #537537;
    letter-spacing: 3px; }
  .github .github-input {
    margin: 10px;
    font-size: 17px; }
  .github .github-button {
    margin: 10px;
    font-size: 17px; }
  .github .github-data {
    font-size: 20px; }
  .github .github-table {
    width: 60%;
    margin: auto;
    text-align: center; }
    .github .github-table a {
      color: #000;
      text-decoration: none; }

.cart-page {
  width: 100%;
  text-align: center;
  position: absolute;
  top: 30%;
  left: 50%;
  transform: translate(-50%, -30%);
  -webkit-transform: translate(-50%, -30%);
  -moz-transform: translate(-50%, -30%);
  -ms-transform: translate(-50%, -30%);
  font-size: 20px; }
  .cart-page .cart-table {
    width: 60%;
    margin: auto;
    text-align: center; }
    .cart-page .cart-table caption {
      margin-bottom: 20px; }
    .cart-page .cart-table a {
      color: #000;
      text-decoration: none; }

